.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.7);
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
  }
  .modal.active {
    opacity: 1;
    pointer-events: all;
  
  }
  .modal_content {
    padding: 35px;
    border-radius: 5px;
    background-color: white;
    width: 50vw;
    transform: scale(0.5);
    transition: 0.4s all;
  }
  .modal_content.active {
    transform: scale(1);
  }